<template>
  <div>
    <div class="news">
      <p class="title">账号中心</p>
      <div class="content">
        <div>
          <span>填写密码：</span>
          <Input type="password" placeholder="新密码" v-model="password1" style="width: auto" />
        </div>
        <br />
        <br />
        <div class="cl"></div>
        <div>
          <span>确认密码：</span>
          <Input
            type="password"
            placeholder="确认密码"
            v-model="formData.password"
            style="width: auto"
          />
        </div>
        <br />
        <br />
        <div class="cl"></div>
        <div>
          <span>手机号：</span>
          <Input v-model="phone" disabled style="width: auto" />
        </div>
        <br />
        <br />
        <div class="cl"></div>
        <div style="margin-top: 6px">
          <span>验证码：</span>
          <Input placeholder="验证码" v-model="formData.code" style="width: auto;margin-right:20px;" />
          <Button
            @click="getModifyCode"
            :disabled="countDownNub != 60"
            type="primary"
          >{{countDownNub == 60 ? '获取验证码' : `${countDownNub}秒后重新获取`}}</Button>
        </div>
        <br />
        <br />
        <Button @click="submit" style="margin-left:100px;" type="primary">确认提交</Button>
      </div>
    </div>
  </div>
</template>
<script>
import { Button, Input } from "iview";
import { getModifyCode, changePassword } from "../../../api/user";
import { messages } from "../../../utils";
import { mapActions, mapState } from "vuex";
export default {
  components: {
    [Button.name]: Button,
    [Input.name]: Input
  },
  computed: {
    ...mapState("user", ["userInfo"])
  },
  data() {
    return {
      countDownNub: 60,
      phone: null,
      password1: "",
      formData: {
        password: "",
        code: ""
      }
    };
  },
  methods: {
    ...mapActions("app", ["Logout"]),
    /**
     * @function 获取验证码
     * @param { phone 手机号 }
     */
    getModifyCode() {
      getModifyCode({ phone: this.phone }).then(res => {
        if (res.data.status == "SUCCESS") {
          messages.success(
            `验证码获取成功,验证码10分钟内有效 ${res.data.data.code}`
          );
          this.countDown();
        }
      });
    },
    // 倒计时
    countDown() {
      this.countDownNub = this.countDownNub - 1;
      if (this.countDownNub == 0) {
        this.countDownNub = 60;
        return;
      }
      let _this = this;
      setTimeout(function() {
        _this.countDown();
      }, 1000);
    },
    submit() {
      if (this.formData.password != this.password1) {
        return messages.error(`两次密码不一样`);
      }
      if (this.formData.password1 != "" && this.formData.code != "") {
        changePassword(this.formData).then(res => {
          if (res.data.status == "SUCCESS") {
            this.Logout().then(() => {
              messages.success(`密码修改成功,请重新登录`);
              this.$router.push("/login");
            });
          }
        });
      } else {
        messages.error(`新密码和验证码必须全部填写`);
      }
    }
  },
  created() {
    this.phone = this.userInfo.phone;
  }
};
</script>

<style lang="less" scoped>
.news {
  margin-top: 20px;
  width: 100%;
  background: #ffffff;
  padding: 20px 0px;
  .title {
    border-left: 5px solid #575757;
    padding-left: 10px;
    color: #2868d1;
    font-size: 16px;
    margin-left: 20px;
  }
  .content {
    padding: 50px 100px;
    min-height: 425px;
    span {
      width: 100px;
      display: block;
      float: left;
      margin-bottom: 20px;
      line-height: 20px;
    }
  }
}
</style>

